.form{
   bottom: 0;
   right:150px;
   position: absolute;
    transform: translate(0%,-50%);
    transition: all 0.5s;
    width: 30px;
    height: 30px;
   
    box-sizing: border-box;
    border-radius: 25px;
    padding: 1px;
}

.input{
    top: 0;
       position: absolute;
    left: 0;
    width: 100%;;
    height: 30px;
    line-height: 30px;
    outline: 0;
    border: 0;
    display: none;
    font-size: 1em;
    border-radius: 20px;
    padding: 0 20px;
}
.box{
    display: none;
}

.fa{
    box-sizing: border-box;
    padding: 4.7px;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    right:0;

    border-radius: 50%;
    color: white;
     font-size: 1.2em;
    transition: all 1s;
}

.form:hover{
    width: 200px;
    cursor: pointer;
}

.form:hover .input{
    display: block;
}

.form:hover .box{
    display: block;
}

.form:hover .fa{
    background: rgb(21, 10, 43);
    color: white;
}


@media (min-width: 600px) {
	.form{
	  right:210px;
   		width: 40px;
    	height: 40px;
    	padding: 4px;
	}
	.fa{
		right: 0;
   text-align: center;
   padding: 10px;
   width: 42.5px;
    height: 42.5px;

	}
.input{
    height: 42.5px;
}
}
