.userprofile{
	height: 500px;
	
}
.userp{
	overflow-y:  scroll;
	top: 0%;
}
.stc{
	position: sticky;
	top: 0px;
}


.userp::-webkit-scrollbar {
    width: 0.7em;
}
 
.userp::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
 
.userp::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 0.7px solid rgb(21, 10, 43);
}

.questionCard{
	margin-top: -40px;
	float: left;
    display: inline-block;
    padding: 15px;
    margin-left: 20px;
    margin-right: 0;
    border-radius: 3px;
}