.mbmenu{
	transition: 0.5s ; 
	 border:'1px solid black';
	  height: 85%;
	  color: black;
	  overflow: scroll;
}

.mbmenu::-webkit-scrollbar {
    width: 0.7em;
}
 
.mbmenu::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
 
.mbmenu::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 0.7px solid rgb(21, 10, 43);
}